body {
  font-family: 'Flamenco', cursive;
}

.signupScreen {
  max-width: 340px;
  padding: 30px;
  margin: 0 auto;
  background: rgba(0, 0, 0, 0.51);
}

.signupScreen > form {
  display: grid;
  flex-direction: column;
}

.signupScreen > form > h1 {
  text-align: left;
  margin-bottom: 25px;
  margin-top: -10px;
}

.signupScreen > form > input {
  outline-width: 0;
  height: 40px;
  margin-bottom: 14px;
  border-radius: 5px;
  border: none;
  padding: 5px 15px;
  /* padding: 3px 15px; */
  font-family: 'Flamenco', cursive;
  font-size: 18px;
}

.signupScreen > form > div > p {
  margin-top: 0;
}

.signupScreen > form > button {
  padding: 16px 26px;
  /* padding: 14px 26px; */
  font-size: 1rem;
  color: #fff;
  border-radius: 5px;
  background-color: #e50914;
  border: none;
  cursor: pointer;
  margin-top: 20px;
  /* margin-top: 15px; */
  font-family: 'Flamenco', cursive;
}

.signupScreen > form > h4 {
  text-align: left;
  margin-top: 30px;
  /* margin-top: 24px; */
}

.signupScreen__gray {
  color: gray;
}
.signupScreen__link:hover {
  cursor: pointer;
  text-decoration: underline;
}